@font-face {
  font-family: 'my-cv';
  src:
    url('asset/font//my-cv.ttf?9kldrw') format('truetype'),
    url('asset/font/.woff?9kldrw') format('woff'),
    url('asset/font/my-cv.svg?9kldrw#my-cv') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="my-cv__icon--"], [class*=" my-cv__icon--"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'my-cv' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

